button:hover {
    background-color: #f58634;
    color: white;
  }
  
  .btn {
    padding: 1rem;
    border: 2px solid #f58634;
    background-color: transparent;
    color: white;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s ease-in;
    width: fit-content;
  }
  
  @media screen and (max-width: 1024px) {
    .btn {
      font-size: 1.2rem;
    }
  }
  
  @media screen and (max-width: 768px) {
    .btn {
      font-size: 1rem;
    }
  }
  
  @media screen and (max-width: 578px) {
    .btn {
      margin: auto;
    }
  }
  